@import url(https://fonts.googleapis.com/css2?family=Hind+Siliguri:wght@300;400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
.r,
.g,
.b {
	width: 32px;
	height: 32px;
	border: none;
	outline: none;
	border-radius: 50%;
	overflow: hidden;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.r {
	background-color: rgb(53, 0, 0);
	color: rgb(53, 0, 0);
}
.g {
	background-color: rgb(9, 24, 0);
	color: rgb(9, 24, 0);
}
.b {
	background-color: rgb(2, 0, 32);
	color: rgb(2, 0, 32);
}

button:nth-child(2) {
	margin: 0rem 0.2rem;
}
.bordered {
	border: 0.1rem solid rgb(12, 12, 12) 0.1rem solid rgb(204, 204, 204);
}
input {
	border-radius: 50%;
	margin: 0rem 0.2rem;
	width: 32px;
	height: 32px;
	border: none;
	outline: none;
	border-radius: 50%;
}

input[type='color']::-webkit-color-swatch {
	border: none;
	border-radius: 50%;
	padding: 0;
}

body {
	font-family: 'Hind Siliguri', sans-serif !important;
}
h1 {
	color: rgb(0, 174, 255);
}

